.method {
  &__content-wrapper {
    padding-top: 100px;
  }

  &__content {
    & h3 {
      margin: 0;

      font-family: "Info", "Arial", sans-serif;
      font-weight: 400;
      font-size: 18px;
      text-transform: uppercase;
    }

    & p {
      margin: 30px 0 0;
    }
  }

  &__visualization-wrapper {
    padding-top: 60px;
  }

  &__visualization-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  &__visualization-item {
    width: 33.3%;

    &:nth-child(-n + 3) {
      margin-bottom: 35px;
    }

    &:nth-child(3n) {
      margin-right: 0;
    }

    & img {
      display: block;
      margin: 0 auto;
    }
  }
}

@media (min-width: 700px) {
  .method {
    &__content-wrapper {
      padding-top: 50px;
    }

    &__content {
      & h3 {
        font-size: 16px;
      }

      & p {
        margin: 20px 0 0;

        font-size: 14px;
        line-height: 26px;
      }
    }

    &__visualization-wrapper {
      padding-top: 40px;
    }

    &__visualization-item {
      width: 16.6%;
    }
  }
}

